/* src/components/Home.css */

.home-container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%); /* Gradient background */
    color: #fff;
    text-align: center;
    position: relative;
    overflow: hidden;
  }
  
  .overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.6); /* semi-transparent overlay */
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
  }
  
  .content {
    position: relative;
    z-index: 2;
    backdrop-filter: blur(10px); /* Glassmorphism effect */
    background: rgba(255, 255, 255, 0.2); /* Translucent background */
    padding: 40px 60px;
    border-radius: 15px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
    animation: slideIn 1.5s ease; /* Slide-in animation */
  }
  
  h1 {
    font-size: 3em;
    font-weight: 700;
    margin-bottom: 0.5em;
    letter-spacing: 1px;
    color: #ffd700;
    text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.7);
  }
  
  p {
    font-size: 1.5em;
    margin-bottom: 2em;
    color: #ddd;
    text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.6);
  }
  
  nav {
    display: flex;
    gap: 1em;
  }
  
  .nav-link {
    display: inline-block;
    padding: 0.8em 1.5em;
    font-size: 1.2em;
    color: #ffd700;
    background-color: rgba(255, 255, 255, 0.2); /* Glassy button effect */
    border: 2px solid #ffd700;
    border-radius: 25px;
    text-decoration: none;
    transition: all 0.3s ease;
    backdrop-filter: blur(5px); /* Enhance glassmorphism effect */
  }
  
  .nav-link:hover {
    color: #333;
    background-color: #ffd700;
    border-color: #ffd700;
    transform: scale(1.1); /* Hover effect with scaling */
  }
  
  @keyframes slideIn {
    from {
      opacity: 0;
      transform: translateY(-50px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  @media (max-width: 768px) {
    h1 {
      font-size: 2.2em;
    }
  
    p {
      font-size: 1.2em;
    }
  
    .nav-link {
      font-size: 1em;
      padding: 0.6em 1.2em;
    }
  }
  